import { supabaseAdmin } from './supabase/admin';
import { createClient } from './supabase/client';
import { Alias, AliasWithUser, Rule, LogStatus, Log } from '@/types/database';

const supabase = createClient();

export const getAliasWithUser = async (address: string): Promise<AliasWithUser | null> => {
  const { data, error } = await supabaseAdmin
    .from('aliases')
    .select('*, users(*)')
    .eq('address', address)
    .single();

  if (error || !data) return null;
  return data as unknown as AliasWithUser;
};

export const getAliases = async (): Promise<Alias[]> => {
  const { data, error } = await supabase
    .from('aliases')
    .select('*')
    .order('created_at', { ascending: false });

  if (error) throw error;
  return data || [];
};

export const createAlias = async (address: string) => {
  const { data: { user } } = await supabase.auth.getUser();
  if (!user) throw new Error('Not authenticated');

  const { data, error } = await supabase
    .from('aliases')
    .insert({ address, user_id: user.id })
    .select()
    .single();

  if (error) throw error;
  return data;
};

export const toggleAliasStatus = async (id: string, isActive: boolean) => {
  const { error } = await supabase
    .from('aliases')
    .update({ is_active: isActive })
    .eq('id', id);

  if (error) throw error;
};

export const deleteAlias = async (id: string) => {
  const { error } = await supabase
    .from('aliases')
    .delete()
    .eq('id', id);

  if (error) throw error;
};

export const getRulesForAlias = async (aliasId: string): Promise<Rule[]> => {
  const { data, error } = await supabaseAdmin
    .from('rules')
    .select('*')
    .eq('alias_id', aliasId);

  if (error || !data) return [];
  return data as Rule[];
};

export const getAllRules = async (): Promise<Rule[]> => {
  const { data, error } = await supabase
    .from('rules')
    .select('*, aliases(address)')
    .order('created_at', { ascending: false });

  if (error) throw error;
  return data as any;
};

export const createRule = async (aliasId: string, pattern: string, action: 'allow' | 'block') => {
  const { data, error } = await supabase
    .from('rules')
    .insert({ alias_id: aliasId, pattern, action })
    .select()
    .single();

  if (error) throw error;
  return data;
};

export const deleteRule = async (id: string) => {
  const { error } = await supabase
    .from('rules')
    .delete()
    .eq('id', id);

  if (error) throw error;
};

export const logEmailActivity = async (payload: {
  alias_id: string;
  sender: string;
  subject: string;
  status: LogStatus;
  ai_summary?: string;
}) => {
  return supabaseAdmin.from('logs').insert(payload);
};

export const getLogs = async (): Promise<(Log & { aliases: { address: string } })[]> => {
  const { data, error } = await supabase
    .from('logs')
    .select('*, aliases(address)')
    .order('created_at', { ascending: false });

  if (error) throw error;
  return data as any;
};
